<template>
  <div id="app" class="page search">
    <div class="container">
      <Loading :hide="hide"></Loading>
      <div class="page-content">
        <section class="showcase">
          <h2>Busca por {{search}}</h2>
          <div class="container p-3" v-if="municipios">
            <h3 class="mb-5"> Municípios </h3>
            
            <div v-for="municipio in municipios" v-bind:key="municipio.nome" class="dep">
              <router-link :to="{ name: 'municipio', params: { id: municipio.codigoIbge, ano: anoAtual } }" class="dropdown-item">
                {{municipio.nome}} - {{municipio.uf}}
              </router-link>
            </div>
          </div>
          <div class="container p-3 mt-5 mb-5" v-else>
            <div style="text-align:center">
              <h2>Município não encontrado.</h2>  Por favor, <router-link to="/">tente novamente</router-link>.
            </div>
            <div class="col-md-12 mx-auto">
              <form method="get" action="search.php">
                <div class="form-row">
                  <div class="col-12 mb-2 mb-md-0">
                    <input type="text" name="s" class="form-control form-control-lg" placeholder="Insira seu município">
                  </div>
                  <div class="col-12 mb-2 mb-md-0">
                  </div>
                </div>
              </form>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import Loading from '@/components/Loading.vue'

  export default {
    name: 'search',
    components: {
      Loading
    },
    props: {
      search: String,
    },
    data: function() {
      const data = {
        municipios: false,
        hide: '',
        anoAtual: new Date().getFullYear()
      };
      return data 
    }, 
    methods: {
      getMunicipios: function(search, anoAtual) {
        this.$http.get( this.$apiUrls.getMunicipiosPorNome(search) ).then((response) => {
          this.municipios = response.data
          if (Array.isArray(this.municipios) && (this.municipios.length==1)) {
            this.$router.push({name: 'municipio', params: {id:this.municipios[0].codigoIbge, ano: anoAtual} });
          }
          this.hide = 'hide'
        }, (error) => {
          this.hide = 'hide';
          console.log(error);
          return false;
        });
        return true;
      }
    },
    mounted() {
      this.getMunicipios(this.search, this.anoAtual);
    }
  }
</script>
